.head-calendar-live {
  background: #D0EAE2;
  padding: 30px 30px 15px 30px;
  border-top-left-radius: 16px;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.head-calendar-live .calendar {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  align-items: center;
  color: var(--colormain);
  font-size: 16px;
  font-weight: 300;
}
.head-calendar-live .calendar .day {
  font-size: 24px;
  font-weight: 500;
}
.head-calendar-live .calendar .day-button {
  display: flex;
  align-items: center;
  gap: 10px;
}
.head-calendar-live .calendar .day-button .prev,
.head-calendar-live .calendar .day-button .next {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--colormain);
  border-radius: 4px;
  cursor: pointer;
}
.head-calendar-live .calendar .day-button .prev svg,
.head-calendar-live .calendar .day-button .next svg {
  width: 14px;
  height: 14px;
}
.head-calendar-live .week {
  position: absolute;
  font-size: 16px;
  font-weight: 300;
  color: #fff;
  background: url(images/graphics/bg-week1.png) bottom -60px left;
  padding: 0 30px 0 10px;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  text-align: right;
  justify-content: end;
}
.head-calendar-live .week::before {
  content: "";
  background: url(images/graphics/bg-week2.png) bottom 34px left 40px;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 100%;
  height: 100%;
  right: 0;
}
.head-calendar-live .week .number {
  font-size: 24px;
  font-weight: 500;
  width: 100%;
}
@media (max-width: 1480px) {
  .head-calendar-live {
    padding: 20px;
  }
}
@media (max-width: 768px) {
  .head-calendar-live .today {
    display: none;
  }
}
@media (max-width: 600px) {
  .head-calendar-live {
    padding: 8px 16px;
    border-radius: 4px;
  }
  .head-calendar-live .calendar {
    gap: 8px;
  }
  .head-calendar-live .calendar .day {
    width: 100%;
  }
}

.calendar-live-list .view-backday,
.calendar-live-list .itemt {
  border-radius: 4px;
  padding: 16px;
  margin-bottom: 16px;
  background: #F1F8F5;
  border: 1px solid #DBE2DF;
  width: 100%;
  display: flex;
  align-items: center;
}
.calendar-live-list .view-backday {
  font-size: 16px;
  justify-content: space-between;
}
.calendar-live-list .view-backday .icon-svg {
  width: 16px;
  height: 16px;
  color: #656867;
  transform: rotate(-90deg);
}
.calendar-live-list .itemt {
  gap: 20px;
}
.calendar-live-list .itemt .day {
  flex-shrink: 0;
  font-size: 20px;
  color: var(--color);
}
.calendar-live-list .itemt .list {
  margin-bottom: 0;
  width: 100%;
}
.calendar-live-list .itemt .list > li {
  background: #fff;
  position: relative;
  padding: 16px;
  border-radius: 6px;
  margin-bottom: 16px;
  border-left: 2px solid var(--color);
}
.calendar-live-list .itemt .list > li:last-child {
  margin-bottom: 0;
}
.calendar-live-list .itemt .line-bottom.mt10 {
  margin-left: -16px;
  margin-right: -16px;
}
.calendar-live-list .itemt .flex.color-gray .flexbox {
  position: relative;
  padding: 2px 0;
}
.calendar-live-list .itemt .flex.color-gray .flexbox span {
  display: none;
}
.calendar-live-list .itemt .flex.color-gray .flexbox.flexbox + .flexbox::before {
  content: "";
  background: #dbe2df;
  position: absolute;
  width: 1px;
  height: 25px;
  left: -15px;
}
.calendar-live-list .itemt .btn-live .wrap {
  color: #fff;
  font-size: 16px;
  font-weight: 300;
  cursor: pointer;
  position: relative;
  border-radius: 4px;
  background: #ABB0AE;
  padding: 6px 12px;
  height: 100%;
  width: 140px;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
}
.calendar-live-list .itemt .btn-live .wrap * {
  position: relative;
  z-index: 1;
}
.calendar-live-list .itemt .btn-live .ic {
  flex-shrink: 0;
  background: url(images/graphics/dotted-white.png) no-repeat top;
  padding-top: 23px;
  text-align: center;
}
.calendar-live-list .itemt .btn-live.on .wrap {
  color: var(--colormain);
}
.calendar-live-list .itemt .btn-live.on .wrap::before {
  content: "";
  background: linear-gradient(279deg, #F9A826 0%, #68BBA0 100%);
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  border-radius: 4px;
}
.calendar-live-list .itemt .btn-live.on .wrap::after {
  content: "";
  background: #fff;
  position: absolute;
  left: 1px;
  top: 1px;
  right: 1px;
  bottom: 1px;
  border-radius: 4px;
}
.calendar-live-list .itemt .btn-live.on .ic {
  color: #F9A826;
  background: url(images/graphics/dotted-oranger.png) no-repeat top;
}
.calendar-live-list .itemt .btn-live.live .wrap {
  color: #fff;
  background: linear-gradient(279deg, #F9A826 0%, #68BBA0 100%);
}
.calendar-live-list .itemt .btn-live.live .ic {
  background: url(images/graphics/dotted-white.png) no-repeat top;
}
.calendar-live-list .itemt:first-child .flex.color-gray {
  color: #2D8755;
}
.calendar-live-list .itemt:nth-child(2) .day, .calendar-live-list .itemt:nth-child(5) .day {
  color: #A674F8;
}
.calendar-live-list .itemt:nth-child(2) .list > li, .calendar-live-list .itemt:nth-child(5) .list > li {
  border-left: 2px solid #A674F8;
}
.calendar-live-list .itemt:nth-child(3) .day, .calendar-live-list .itemt:nth-child(6) .day {
  color: #034C31;
}
.calendar-live-list .itemt:nth-child(3) .list > li, .calendar-live-list .itemt:nth-child(6) .list > li {
  border-left: 2px solid #68BBA0;
}
@media (max-width: 768px) {
  .calendar-live-list .itemt {
    display: block;
  }
  .calendar-live-list .itemt .day {
    margin-bottom: 8px;
  }
}
@media (max-width: 600px) {
  .calendar-live-list .view-backday {
    display: none;
  }
  .calendar-live-list .itemt {
    padding: 0;
    border: 0;
  }
  .calendar-live-list .itemt .day {
    font-size: 16px;
  }
  .calendar-live-list .itemt .list > li {
    padding: 8px 16px;
    margin-bottom: 8px;
  }
  .calendar-live-list .itemt .gap_16 {
    column-gap: 6px !important;
    row-gap: 6px !important;
  }
  .calendar-live-list .itemt .top-line {
    flex-wrap: nowrap;
    column-gap: 6px !important;
    row-gap: 6px !important;
  }
  .calendar-live-list .itemt .flex.color-gray {
    column-gap: 16px !important;
    row-gap: 16px !important;
  }
  .calendar-live-list .itemt .flex.color-gray .flexbox {
    font-size: 0;
    column-gap: 3px !important;
    row-gap: 3px !important;
  }
  .calendar-live-list .itemt .flex.color-gray .flexbox span {
    display: block;
    font-size: 13px;
  }
  .calendar-live-list .itemt .flex.color-gray .flexbox.flexbox + .flexbox::before {
    height: 24px;
    left: -8px;
  }
  .calendar-live-list .itemt .view {
    display: none;
  }
  .calendar-live-list .itemt .btn-live {
    flex-shrink: 0;
  }
  .calendar-live-list .itemt .btn-live .wrap {
    width: 75px;
    font-size: 12px;
  }
  .calendar-live-list .itemt .btn-live .ic {
    max-width: 75px;
  }
}